<template>
  <div>

      <b-container>
          <b-breadcrumb :items="items"/>
          <b-form @submit="onSubmit" novalidate>
            <b-form-group id="emailGroup"
                          label="Email:"
                          label-for="email"
                          description="Informe seu email"
                          :invalid-feedback="feedbackEmail"
                          :state="submited && $v.form.email.$error ? false : null">
              <b-form-input id="email"
                            type="email"
                            v-model="form.email"
                            required
                            placeholder="Digite o email"
                            :state="submited && $v.form.email.$error ? false : null">
              </b-form-input>
            </b-form-group>

            <b-form-group id="nameGroup"
                          label="Nome:"
                          label-for="name"
                          description="Informe o seu primeiro nome"
                          :invalid-feedback="feedbackName"
                          :state="submited && $v.form.name.$error ? false : null">
              <b-form-input id="name"
                            type="text"
                            v-model="form.name"
                            required
                            :state="submited && $v.form.name.$error ? false : null"
                            placeholder="Digite o nome">
              </b-form-input>
            </b-form-group>

            <b-form-group id="lastnameGroup"
                          label="Sobrenome:"
                          label-for="lastName"
                          :invalid-feedback="feedbackLastName"
                          :state="submited && $v.form.lastName.$error ? false : null"
                          description="Informe o seu primeiro nome">
              <b-form-input id="lastName"
                            type="text"
                            v-model="form.lastName"
                            required
                            :state="submited && $v.form.lastName.$error ? false : null"
                            placeholder="Digite o sobrenome">
              </b-form-input>
            </b-form-group>

            <b-form-group id="usernameGroup"
                          label="Login:"
                          label-for="username"
                          :invalid-feedback="feedbackUsername"
                          :state="submited && $v.form.username.$error ? false : null"
                          description="Um usurio para realizar login">
              <b-form-input id="username"
                            type="text"
                            v-model="form.username"
                            required
                            :state="submited && $v.form.username.$error ? false : null"
                            placeholder="Digite o nome de usurio">
              </b-form-input>
            </b-form-group>

            <b-form-group id="passwordGroup"
                          label="Senha:"
                          label-for="password"
                          description="Senha do usurio"
                          :invalid-feedback="feedbackPassword"
                          :state="submited && $v.form.password.$error ? false : null">
              <b-form-input id="password"
                            type="password"
                            v-model="form.password"
                            required
                            placeholder="Digite a senha"
                            :state="submited && $v.form.password.$error ? false : null" >
              </b-form-input>
            </b-form-group>

            <b-form-group id="confirmPasswordGroup"
                          label="Confirmar senha:"
                          label-for="confirmPassword"
                          description="Confirmar Senha do usurio"
                          :invalid-feedback="feedbackConfirmPassword"
                          :state="submited && $v.form.password_confirm.$error ? false : null">
              <b-form-input id="confirmPassword"
                            type="password"
                            v-model="form.password_confirm"
                            required
                            placeholder="Confirmar senha">
              </b-form-input>
            </b-form-group>

            <b-button class="right" type="submit" variant="primary">Salvar</b-button>
        </b-form>
      </b-container>
  </div>
</template>

<script>
import { http } from '@/services/http';
import config from '@/config/api-settings';
import toastr from 'toastr';
import {
  required,
  email,
  minLength,
  sameAs,
  alphaNum
} from 'vuelidate/lib/validators';
import messages from '@/services/messages';

export default {
  name: 'NewUser',
  data() {
    return {
      submited: false,
      form: {
        id: '',
        email: '',
        name: '',
        lastName: '',
        username: '',
        password: '',
        password_confirm: ''
      },
      items: [
        {
          text: 'Lista de Usurios',
          href: '/users'
        },
        {
          text: 'Novo Usurio',
          active: true
        }
      ]
    };
  },
  created() {},
  methods: {
    async onSubmit(e) {
      e.preventDefault();
      this.submited = true;
      this.$v.form.$touch();
      if (!this.$v.form.$error) {
        try {
          const res = await http.post(config.ENDPOINTS.POST_USER, this.form);
          toastr.success(res.data.html, res.data.title);
          this.$router.replace('/users');
        } catch (err) {
          toastr.error(err.response.data.html, err.response.data.title);
        }
      }
    }
  },
  computed: {
    feedbackEmail() {
      if (this.$v.form.email.$error) {
        if (!this.$v.form.email.required) {
          return messages.required;
        }
        if (!this.$v.form.email.email) {
          return this.messages.invalidEmail;
        }
      }
      return '';
    },

    feedbackName() {
      if (this.$v.form.name.$error) {
        if (!this.$v.form.name.required) {
          return messages.required;
        }
      }
      return '';
    },
    feedbackLastName() {
      if (this.$v.form.lastName.$error) {
        if (!this.$v.form.lastName.required) {
          return messages.required;
        }
      }
      return '';
    },
    feedbackUsername() {
      if (this.$v.form.username.$error) {
        if (!this.$v.form.username.required) {
          return messages.required;
        }
      }
      return '';
    },
    feedbackPassword() {
      if (this.$v.form.password.$error) {
        if (!this.$v.form.password.required) {
          return messages.required;
        }
        if (!this.$v.form.password.alphaNum) {
          return messages.alphaNum;
        }
        if (!this.$v.form.password.minLength) {
          return messages.minLength(6);
        }
      }
      return '';
    },
    feedbackConfirmPassword() {
      if (this.$v.form.password_confirm.$error) {
        if (!this.$v.form.password_confirm.required) {
          return messages.required;
        }
        if (!this.$v.form.password_confirm.sameAsPassword) {
          return messages.sameAs;
        }
      }
      return '';
    }
  },
  validations: {
    form: {
      email: {
        required,
        email
      },
      name: {
        required
      },
      lastName: {
        required
      },
      username: {
        required
      },
      password: {
        required,
        alphaNum,
        minLength: minLength(8)
      },
      password_confirm: {
        required,
        sameAsPassword: sameAs('password')
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import '~assets/scss/index.scss';
</style>




