<template>
  <div>

      <b-container>
         <b-breadcrumb :items="items"/>
          <b-form @submit="onSubmit" novalidate="">
            <b-form-group id="emailGroup"
                          label="Email:"
                          label-for="email"
                          description="Informe seu email"
                          :invalid-feedback="feedbackEmail"
                          :state="submited && $v.form.email.$error ? false : null">
              <b-form-input id="email"
                            type="email"
                            v-model="form.email"
                            required
                            placeholder="Digite o email"
                            :state="submited && $v.form.email.$error ? false : null">
              </b-form-input>
            </b-form-group>

            <b-form-group id="nameGroup"
                          label="Nome:"
                          label-for="name"
                          description="Informe o seu primeiro nome"
                          :invalid-feedback="feedbackName"
                          :state="submited && $v.form.name.$error ? false : null">
              <b-form-input id="name"
                            type="text"
                            v-model="form.name"
                            required
                            :state="submited && $v.form.name.$error ? false : null"
                            placeholder="Digite o nome">
              </b-form-input>
            </b-form-group>

            <b-form-group id="lastnameGroup"
                          label="Sobrenome:"
                          label-for="lastName"
                          :invalid-feedback="feedbackLastName"
                          :state="submited && $v.form.lastName.$error ? false : null"
                          description="Informe o seu primeiro nome">
              <b-form-input id="lastName"
                            type="text"
                            v-model="form.lastName"
                            required
                            :state="submited && $v.form.lastName.$error ? false : null"
                            placeholder="Digite o sobrenome">
              </b-form-input>
            </b-form-group>

            <b-form-group id="usernameGroup"
                          label="Login:"
                          label-for="username"
                          :invalid-feedback="feedbackUsername"
                          :state="submited && $v.form.username.$error ? false : null"
                          description="Um usurio para realizar login">
              <b-form-input id="username"
                            type="text"
                            v-model="form.username"
                            required
                            :state="submited && $v.form.username.$error ? false : null"
                            placeholder="Digite o nome de usurio">
              </b-form-input>
            </b-form-group>


            <b-button class="right" type="submit" variant="primary">Salvar</b-button>
        </b-form>
      </b-container>
  </div>
</template>

<script>
import { http } from '@/services/http';
import config from '@/config/api-settings';
import toastr from 'toastr';
import { required, email } from 'vuelidate/lib/validators';
import messages from '@/services/messages';

export default {
  name: 'EditUser',
  data() {
    return {
      submited: false,
      form: {
        id: '',
        email: '',
        name: '',
        lastName: '',
        username: ''
      },
      items: [
        {
          text: 'Lista de Usurios',
          href: '/users'
        },
        {
          text: 'Editar Usurio',
          active: true
        }
      ]
    };
  },
  async created() {
    const id = this.$route.params.id;
    this.id = id;
    const res = await http.get(`${config.ENDPOINTS.GET_USER(id)}`);
    this.form = res.data;
  },
  methods: {
    async onSubmit(e) {
      e.preventDefault();
      this.submited = true;
      this.$v.form.$touch();
      if (!this.$v.form.$error) {
        try {
          await http.patch(config.ENDPOINTS.EDIT_USER(this.id), this.form);
          toastr.success('Operao realizada com sucesso');
          this.$router.replace('/users');
        } catch (err) {
          toastr.error(err.response.data.html, err.response.data.title);
        }
      }
    }
  },
  computed: {
    feedbackEmail() {
      if (this.$v.form.email.$error) {
        if (!this.$v.form.email.required) {
          return messages.required;
        }
        if (!this.$v.form.email.email) {
          return this.messages.invalidEmail;
        }
      }
      return '';
    },

    feedbackName() {
      if (this.$v.form.name.$error) {
        if (!this.$v.form.name.required) {
          return messages.required;
        }
      }
      return '';
    },
    feedbackLastName() {
      if (this.$v.form.lastName.$error) {
        if (!this.$v.form.lastName.required) {
          return messages.required;
        }
      }
      return '';
    },
    feedbackUsername() {
      if (this.$v.form.username.$error) {
        if (!this.$v.form.username.required) {
          return messages.required;
        }
      }
      return '';
    }
  },
  validations: {
    form: {
      email: {
        required,
        email
      },
      name: {
        required
      },
      lastName: {
        required
      },
      username: {
        required
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import '~assets/scss/index.scss';
</style>




