import Vue from 'vue';
import Router from 'vue-router';
import Login from '@/pages/login/Login';
import CampaignsList from '@/pages/campaigns/CampaignsList';
import NewCampaign from '@/pages/campaigns/NewCampaign';
import UploadNewCampaign from '@/pages/campaigns/UploadNewCampaign';
import EditCampaign from '@/pages/campaigns/EditCampaign';
import UsersList from '@/pages/users/UsersList';
import EditUser from '@/pages/users/EditUser';
import NewUser from '@/pages/users/NewUser';
import Error from '@/pages/error/Error';

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login,
      meta: { title: 'Login' }
    },
    {
      path: '/campaigns',
      name: 'CampaignsList',
      component: CampaignsList,
      meta: { title: 'Pgina Inicial' }
    },
    {
      path: '/campaigns/new',
      name: 'NewCampaign',
      component: NewCampaign,
      meta: { title: 'Criar campanha' }
    },
    {
      path: '/campaigns/upload',
      name: 'UploadNewCampaign',
      component: UploadNewCampaign,
      meta: { title: 'Upload campanha' }
    },
    {
      path: '/campaigns/upload/:id',
      name: 'UploadNewCampaign',
      component: UploadNewCampaign,
      meta: { title: 'Upload campanha' }
    },
    {
      path: '/campaigns/edit/:id',
      name: 'EditCampaign',
      component: EditCampaign,
      meta: { title: 'Editar campanha' }
    },
    {
      path: '/users',
      name: 'UsersList',
      component: UsersList,
      meta: { title: 'Lista de Usurios' }
    },
    {
      path: '/users/new',
      name: 'NewUser',
      component: NewUser,
      meta: { title: 'Cadastrar usurio' }
    },
    {
      path: '/users/edit/:id',
      name: 'EditUser',
      component: EditUser,
      meta: { title: 'Editar usurio' }
    },
    {
      path: '*',
      name: 'Error',
      component: Error
    }
  ],
  scrollBehavior(to) {
    if (to.hash) {
      return {
        selector: to.hash
      };
    }
    return { x: 0, y: 0 };
  }
});



