<template>
  <div>
      <b-container>
         <b-breadcrumb :items="items"/>
          <b-form @submit="onSubmit" novalidate >
            <b-form-group id="titleGroup"
                          label="Nome da Campanha:"
                          label-for="title"
                          description="D um nome para a sua campanha"
                          :invalid-feedback="feedbackTitle"
                          :state="submited && $v.form.title.$error ? false : null">
              <b-form-input id="title"
                            type="text"
                            v-model="form.title"
                            required
                            placeholder="Digite o nome"
                            :state="submited && $v.form.title.$error ? false : null"
                            >
              </b-form-input>
            </b-form-group>

            <b-form-group id="destinationGroup"
                          label="Destinatrios:"
                          label-for="destinations"
                           :invalid-feedback="feedbackNoFileSelected"
                           :state="submited && !form.destinations ? false : null"
                          description="Remetentes, separe os numeros por ;">
              <b-form-file :state="submited && !form.destinations ? false : null" id="destinations" @input="getDestinations" choose-label="Selecione" accept=".txt"></b-form-file>
              <b-btn style="margin-top:10px" v-if="!!form.destinations"  v-b-toggle.collapse1 variant="primary">Ver Nmeros</b-btn>
              <b-collapse id="collapse1" class="mt-2">
                <b-card>
                    <div v-for="(destination, index) in form.destinations"  v-bind:key="index">
                       <p>
                         <strong>Destino:</strong> {{destination.destination}}<br>
                         <strong>Mensagem:</strong> {{destination.message}}
                       </p>
                    </div>
                    <strong></strong>
                </b-card>
              </b-collapse>
            </b-form-group>
            <b-button class="right" type="submit" variant="primary">Salvar</b-button>
        </b-form>
      </b-container>
  </div>
</template>

<script>
import { http } from '@/services/http';
import messages from '@/services/messages';
import config from '@/config/api-settings';
import { required, minLength } from 'vuelidate/lib/validators';
import toastr from 'toastr';

export default {
  name: 'UploadNewCampaign',
  data() {
    return {
      submited: false,
      form: {
        title: '',
        destinations: ''
      },
      items: [
        {
          text: 'Lista de Campanhas',
          href: '/campaigns'
        },
        {
          text: 'Upload Campanha',
          active: true
        }
      ]
    };
  },
  async created() {
    if (this.$route.params.id) {
      const id = this.$route.params.id;
      this.id = id;
      const res = await http.get(`${config.ENDPOINTS.GET_CAMPAINGS}/${id}`);
      this.form = {
        title: res.data.title,
        destinations: res.data.destinations
      };
    }
  },
  methods: {
    onInit: () => {},
    getDestinations(e) {
      const read = new FileReader();
      read.readAsBinaryString(e);
      read.onloadend = () => {
        let result = read.result.split('\n');
        result = result.filter(i => !!i);
        this.form.destinations = result.reduce((accumulator, info) => {
          const data = info.split(',');
          accumulator.push({
            destination: data[0],
            message: data[1]
          });
          return accumulator;
        }, []);
        console.log(this.form.destinations);
      };
    },
    async onSubmit(e) {
      e.preventDefault();
      this.submited = true;
      this.$v.form.$touch();

      const someError = this.form.destinations.some(
        i => !i.message && !i.destination
      );

      if (!this.$v.form.$error && this.form.destinations && !someError) {
        try {
          if (this.id) {
            await http.patch(
              config.ENDPOINTS.UPDATE_CAMPAINGS(this.id),
              this.form
            );
          } else {
            await http.post(config.ENDPOINTS.POST_CAMPAINGS, this.form);
          }
          toastr.success('Operao realizada com sucesso');
          this.$router.replace('/');
        } catch (err) {
          if (err.response) {
            const error = err.response;
            toastr.error(error.data.html, error.data.title);
            console.error('err', err);
            return;
          }
          toastr.error('Erro ao logar');
        }
      }
    }
  },
  computed: {
    feedbackTitle() {
      if (this.$v.form.title.$error) {
        if (!this.$v.form.title.required) {
          return messages.required;
        }
      }
      return '';
    },

    feedbackNoFileSelected() {
      if (!this.destinations) {
        return messages.required;
      }
      return '';
    }
  },
  validations: {
    form: {
      title: {
        required,
        minLength: minLength(4)
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import '~assets/scss/index.scss';
</style>




