<template>
  <div>
      <b-container>
         <b-breadcrumb :items="items"/>
        <b-button size="sm" variant="success" href="users/new" class="bt-right pull-right">
          Novo
        </b-button>
        <b-table ref="table" striped hover :items="users" :fields="fields">
           <template slot="_id" slot-scope="data">
            <b-button size="sm" variant="success" :href="`users/edit/${data.value}`">
              <icon name="pencil"></icon>
            </b-button>
            <b-button size="sm" variant="danger" @click="deleteUser(data.value,data)">
              <icon name="trash"></icon>
            </b-button>
          </template>
        </b-table>
        <b-pagination @input="fetchData($event)" size="sm" :total-rows="total" v-model="currentPage" :per-page="limit">
        </b-pagination>
      </b-container>
  </div>
</template>

<script>
import { http } from '@/services/http';
import config from '@/config/api-settings';
import moment from 'moment';
import swal from 'sweetalert';
// import toastr from 'toastr';

export default {
  name: 'UserList',
  data() {
    return {
      users: [],
      total: 0,
      currentPage: 1,
      limit: 20,
      fields: [
        {
          key: 'name',
          label: 'Nome'
        },
        {
          key: 'email',
          label: 'Email'
        },
        {
          key: 'createdAt',
          label: 'Criado em',
          formatter: value => {
            return moment(value).format('D/M/Y');
          }
        },
        {
          key: '_id',
          label: 'Aes'
        }
      ],
      items: [
        {
          text: 'Lista de Usurios',
          active: true
        }
      ]
    };
  },
  async created() {
    await this.fetchData();
  },
  methods: {
    async fetchData(pageFromPagination) {
      console.log(pageFromPagination);
      let page = 1;
      if (pageFromPagination) {
        page = pageFromPagination;
      }
      const res = await http.get(`${config.ENDPOINTS.GET_USERS}?page=${page}`);
      this.users = res.data.docs;
      this.total = res.data.total;
      this.currentPage = res.data.page;
    },
    deleteUser(id) {
      swal({
        title: 'Confirma a deleo da campanha?',
        icon: 'warning',
        buttons: true,
        dangerMode: true
      }).then(willDelete => {
        if (willDelete) {
          http.delete(config.ENDPOINTS.DELETE_USER(id)).then(() => {
            swal('Usurio deletado com sucesso', {
              icon: 'success'
            });
            // this.$router.replace('/users');
            location.reload();
            // this.$refs.table.refresh();
          });
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@import '~assets/scss/index.scss';

.action-item {
  margin-right: 1rem;
}
</style>




