<template>
     <b-container class="col-md-4 col-md-offset-4 login-container" >
       <div v-if="loading">
      </div>
        <vue-simple-spinner v-if="loading" size="tiny" message="Carregando..."></vue-simple-spinner>

        <b-form @submit="onSubmit" novalidate>
          <b-form-group id="usernameContainer"
                        label="Login:"
                        label-for="username">
            <b-form-input id="username"
                          type="text"
                          v-model="form.username"
                          required
                          placeholder="Digite seu username">
            </b-form-input>
          </b-form-group>

          <b-form-group id="passwordContainer"
                        label="Senha"
                        label-for="password">
            <b-form-input id="password"
                          type="password"
                          v-model="form.password"
                          required
                          placeholder="Digite a senha">
            </b-form-input>
          </b-form-group>

          <!-- <b-link  style="float:right">Recuperar Senha</b-link> -->
          <b-container  class="col-sm-12" style="margin-top:2rem" >
              <b-button v-if="loading" style="width:100%" type="submit" variant="primary" disabled>Entrar</b-button>
              <b-button v-else style="width:100%" type="submit" variant="primary">Entrar</b-button>

          </b-container>
        </b-form>
     </b-container>
</template>

<script>
import { httpNoAuthenticated, http } from '@/services/http';
import storage from '@/services/storage';
import config from '@/config/api-settings';
import toastr from 'toastr';
import VueSimpleSpinner from 'vue-simple-spinner';

export default {
  name: 'Login',
  components: {
    VueSimpleSpinner
  },
  data() {
    return {
      loading: false,
      form: {
        username: '',
        password: ''
      }
    };
  },
  async created() {
    const isLogged = await storage.getToken();
    if (isLogged) {
      this.$router.replace('/campaigns');
    }
  },
  methods: {
    async onSubmit(e) {
      e.preventDefault();
      this.loading = true;
      try {
        const res = await httpNoAuthenticated.post(
          config.AUTH.ENDPOINT_LOGIN,
          this.form
        );
        storage.setAuth(res.data.id, res.data.token);
        const user = await http.get(config.ENDPOINTS.GET_USER(res.data.id));
        storage.setUserFromAPI(user.data);
        this.loading = false;

        this.$router.replace('/campaigns');
      } catch (err) {
        if (err.response) {
          const error = err.response;
          toastr.error(error.data.html, error.data.title);
          console.error('err', err);
          this.loading = false;

          return;
        }
        toastr.error('Erro ao logar');
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import '~assets/scss/index.scss';

.login-container {
  margin-top: 10rem;
  border: 1px solid #ccc;
  height: 20rem;
  padding: 2rem;
  box-shadow: 0px 2px 14px #ccc;
}
</style>




