<template>
  <div>
      <b-container>
        <b-breadcrumb :items="items"/>
        <b-button size="sm" variant="success" href="campaigns/new" class="bt-right pull-right">
          Novo
        </b-button>
        <b-button size="sm" variant="primary" href="campaigns/upload" class="bt-right pull-right">
          Upload Campanha
        </b-button>

        <b-table ref="table" id="table" striped hover :items="campaigns" :fields="fields">
          <template slot="_id" slot-scope="data">
             <b-button size="sm" variant="info" @click="start(data.value)">
              <icon name="paper-plane"></icon>
            </b-button>
            <b-button size="sm" variant="success" :href="getURL(data)">
              <icon name="pencil"></icon>
            </b-button>
            <b-button size="sm" variant="danger" @click="deleteCampaign(data.value,data)">
              <icon name="trash"></icon>
            </b-button>
          </template>
        </b-table>
        <b-pagination @input="fetchData($event)" size="sm" :total-rows="total" v-model="currentPage" :per-page="limit">
        </b-pagination>
      </b-container>
  </div>
</template>

<script>
import { http } from '@/services/http';
import config from '@/config/api-settings';
import moment from 'moment';
import swal from 'sweetalert';
// import toastr from 'toastr';

export default {
  name: 'CampaignsList',
  data() {
    return {
      campaigns: [],
      total: 0,
      currentPage: 1,
      limit: 20,
      fields: [
        {
          key: 'title',
          label: 'Nome'
        },
        {
          key: 'destinations',
          label: 'Destinatarios',
          formatter: value => {
            const destinations = value.map(i => {
              if (i instanceof Object) {
                return i.destination;
              }
              return i;
            });
            return destinations;
          }
        },
        {
          key: 'createdAt',
          label: 'Criado em',
          formatter: value => {
            return moment(value).format('D/M/Y');
          }
        },
        {
          key: '_id',
          label: 'Aes'
        }
      ],
      items: [
        {
          text: 'Lista de Campanhas',
          active: true
        }
      ]
    };
  },
  async created() {
    await this.fetchData();

    /*     try {
    } catch (error) {
      console.error(error.response);
      if (error.response) {
        toastr.error(error.response.data.html, error.response.data.title);
        console.error('err', error.response);
        return;
      }
      toastr.error('Erro ao Recuperar dados');
    }
 */
  },
  methods: {
    async fetchData(pageFromPagination) {
      let page = 1;
      if (pageFromPagination) {
        page = pageFromPagination;
      }
      const res = await http.get(
        `${config.ENDPOINTS.GET_CAMPAINGS}?page=${page}`
      );
      this.campaigns = res.data.docs;
      this.total = res.data.total;
      this.currentPage = res.data.page;
    },
    getURL(data) {
      // console.log(data);
      if (!data.item.message) {
        return `campaigns/upload/${data.value}`;
      }
      return `/campaigns/edit/${data.value}`;
    },
    deleteCampaign(id) {
      swal({
        title: 'Confirma a deleo da campanha?',
        icon: 'warning',
        buttons: true,
        dangerMode: true
      }).then(willDelete => {
        if (willDelete) {
          http.delete(config.ENDPOINTS.DELETE_CAMPAING(id)).then(() => {
            swal('Campanha deletada com sucesso', {
              icon: 'success'
            });
            this.$router.replace('/');
            this.$refs.table.refresh();
          });
        }
      });
    },
    start(id) {
      swal({
        title: 'Confirma o envio da campanha?',
        icon: 'warning',
        buttons: true,
        dangerMode: false
      }).then(willStart => {
        if (willStart) {
          http.post(config.ENDPOINTS.START_CAMPAING(id)).then(() => {
            swal('Campanha iniciada com sucesso', {
              icon: 'success'
            });
            this.$refs.table.refresh();
          });
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@import '~assets/scss/index.scss';

.action-item {
  margin-right: 1rem;
}
</style>




